<template lang="pug">
#project 
  .content-container
    .heading 
      .heading-row
        p Projects
        .heading-row-line
      .heading-column
        .heading-column-line
        .inner-content
          .project-type-selection
            .personal(
              :class="{ 'selected-tab': projectType === 'personal' }",
              @click="selectType('personal')"
            )
              p(:class="{ 'selected-tab-text': projectType === 'personal' }") Personal
            .work(
              :class="{ 'selected-tab': projectType === 'work' }",
              @click="selectType('work')"
            )
              p(:class="{ 'selected-tab-text': projectType === 'work' }") Work
          .projects-container
            .project-el(
              v-for="(project, index) in projectsByType",
              :key="'project-' + index",
              @click="selectProject(project)"
            )
              .project-img
                img(:src="project.coverImage")
</template>

<script>
export default {
  data() {
    return {
      workProjects: [
        "https://family.ikea.com.sg/",
        "https://family.ikea.com.my/",
        "https://family.ikea.co.th/",
        "https://worldmadebetter.kiehls.com.my/join-us-now/",
        "https://brickmagic.asia/",
      ],
      personalProjects: [
        "https://codenames-the-game.web.app/",
        "https://codenames-the-game.web.app/",
        "https://codenames-the-game.web.app/",
      ],
      projectType: "personal",
    };
  },
  computed: {
    projectsByType() {
      if (this.projectType === "work") {
        return this.workProjects;
      } else {
        return this.personalProjects;
      }
    },
  },
  methods: {
    selectProject(project) {
      this.$store.commit("setSelectedProject", project);
      console.log(project);
    },
    selectType(type) {
      this.projectType = type;
    },
    initPersonalProjects() {
      this.personalProjects = [
        {
          coverImage: "https://via.placeholder.com/300x250",
          gif: require("../assets/codenames.gif"),
          projectName: "CodeNames",
          description: `A realtime multiplayer online version of the boardgame Codenames.`,
          motivation: `Prior to the Covid-19 Lockdown in Malaysia, my friends and I would frequently gather at Boardgame Cafes
            to play a bunch of games together. When the Movement-Control Order got announced, we found ourselves really bored - so I decided to take it
            upon myself to re-create the CodeNames Boardgame. Not only did I get to have fun with my friends, I learnt
            more about Firebase and its Realtime Database. `,
          links: ["https://codenames-the-game.web.app/"],
        },
        {
          coverImage: "https://via.placeholder.com/300x250",
          projectName: "ProtoWire [WIP]",
          gif: "https://via.placeholder.com/300x250",
          description: `An online wireframing tool designed to make sketching wireframes easier by having an
            intuitive and easy-to-use UI.`,
          motivation: `I got the idea when I was trying to design other projects and realised how unintuitive 
            online wireframing tools are. My initial idea was to make a simple shape drawing tool to make simple wireframe sketches. However, I soon
            realised there were more and more features I wanted to add - which caused this project to balloon. I now work on this project every now and then as a
            hobby due to its large size.`,
          links: ["https://codenames-the-game.web.app/"],
        },
        {
          coverImage: "https://via.placeholder.com/300x250",
          projectName: "Toggl Timesheet",
          gif: "https://via.placeholder.com/300x250",
          description: `A timesheet tracking tool that makes it easy for employees to submit their timesheet. Using the report
            feature of a time tracking tool called Toggl Track, one can simply upload their report to the website,
            and the weekly time reports will be automatically filled in.`,
          motivation: `I frequently use a time tracking tool known as Toggl Track. It tracks the time I spend on various tasks
     and provides significant insight on how I can improve my productivity. In one of my previous companies, I noticed people had a lot of complaints about the way they submit their
     timesheet. So I decided to put 2 and 2 together to create this app`,
          links: ["https://codenames-the-game.web.app/"],
        },
      ];
    },
    initWorkProjects() {
      this.workProjects = [
        {
          coverImage: "https://via.placeholder.com/300x250",
          projectName: "Kiehls World Made Better",
          gif: require("../assets/kiehls-fmb.gif"),
          description: `A Kiehls microsite developed to promote Kiehl's World Made 
            Better campaign which aims to increase awareness towards environmental 
            impact, sustainable living, and beauty. The microsite involves a virtual 
            world with immersive and interactive features designed to give the user a 
            sense of making impactful changes by performing one's own duty.`,
          responsibilities: [
            "I was one of the main developer in a small team of 3 developers total that built the entire microsite from scratch.",
          ],
          links: ["https://worldmadebetter.kiehls.com.my/join-us-now/"],
        },
        {
          coverImage: "https://via.placeholder.com/300x250",
          projectName: "Brickmagic",
          gif: require("../assets/brickmagic.gif"),
          description: `An e-commerce site for LEGO that allows customers to shop for LEGO products online. 
            It also supports a lego membership system known as VIB Membership.`,
          responsibilities: [
            "Maintained and developed additional features for the admin panel of the site.",
          ],
          links: ["https://brickmagic.asia/"],
        },
        {
          coverImage: "https://via.placeholder.com/300x250",
          projectName: "NikeByYou",
          gif: require("../assets/nby-mov.gif"),
          description: `A Nike microsite that allows users to customize their
            own Nike products. The microsite is only allowed to be used in specific 
            Nike Stores and cannot be accessed by the general public.`,
          responsibilities: [
            "I was the solo developer for all NikeByYou microsites across multiple countries.",
            "Among them include: Singapore, Thailand, and Phillipines",
          ],
          links: [],
        },
        {
          coverImage: "https://via.placeholder.com/300x250",
          projectName: "IKEA Family",
          gif: require("../assets/ikea-family.gif"),
          description: `The purpose of the IKEA Family websites is to provide 
            exclusive offers/activities/information to IKEA Family members. On this site,
            members are able to view special discounts on certain items, as well as 
            access information about their Family membership.`,
          responsibilities: [
            "Improved and optimised various functionalities of the IKEA Family websites",
          ],
          links: [
            "https://family.ikea.com.sg/",
            "https://family.ikea.com.my/",
            "https://family.ikea.co.th/",
          ],
        },
      ];
    },
  },
  mounted() {
    this.initPersonalProjects();
    this.initWorkProjects();
  },
};
</script>

<style lang="sass" scoped>
#project
  width: 100%
  height: 100vh
  position: relative
  border: 1px solid red

.content-container
  position: absolute
  width: 100%
  max-width: 1024px
  transform: translate(-50%, -50%)
  top: 50%
  left: 50%

.project-type-selection
  display: flex
  justify-content: center
  margin-bottom: 20px
  .work
    box-sizing: border-box
    cursor: pointer
    p
      font-size: 20px
  .personal
    margin-right: 50px
    cursor: pointer
    box-sizing: border-box
    p
      font-size: 20px
  .work:hover
    border-bottom: 5px solid #c7e4e9
  .personal:hover
    border-bottom: 5px solid #c7e4e9

.selected-tab
  border-bottom: 5px solid #30DEFF
.selected-tab-text
  color: #30DEFF

.projects-container
  display: flex
  flex-wrap: wrap
  justify-content: center

.project-el
  width: 300px
  flex-grow: 0
  flex-shrink: 0
  height: 250px
  border: 1px solid #fff
  border-radius: 10px
  margin: 10px
  overflow: hidden
  cursor: pointer
  transition: all 1s ease
  transform: translateY(0px)

.project-el:hover
  transform: translateY(-10px)
  box-shadow: 0px 0px 5px 2px #fff
.heading
  text-align: left
  display: flex
  align-items: flex-start
  flex-direction: column
  .heading-row
    display: flex
    align-items: center
    p
      color: #30DEFF
      font-size: 42px
      font-weight: bold
    .heading-row-line
      width: 400px
      height: 1px
      flex-grow: 1
      background-color: #30DEFF
      margin-left: 20px
  .heading-column
    display: flex
    align-items: flex-start
    flex-grow: 1
    .heading-column-line
      width: 1px
      flex-grow: 1
      background-color: #30DEFF
      margin-top: 10px
      margin-left: 20px
      height: 400px
    .inner-content
      padding: 20px 20px 20px 100px
</style>
